.container {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    height: 100vh;
    margin: 0 2rem;

}

.text {}

.text h1 {
    font-weight: bold;
    font-size: 1.7rem;
    padding-bottom: .7rem;

}

.text p {
    font-size: 1.1rem;
    padding-bottom: .7rem;
}

.image img {
    height: 33vw;
}

@media only screen and (max-width:600px) {
    .image {
        display: none;
    }


    .text {
        text-align: center;
    }

    .text h1 {
        font-weight: bold;
        font-size: 1.3rem;
    }

    .text p {
        font-size: 1rem;
    }



}